      background-image: linear-gradient(to right, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.9) 100%), url(/static/img/header.612061a.jpg);
}
}
.book__form {
    width: 50%;
    padding: 6rem;
}
@media only screen and (max-width: 75em) {
.book__form {
        width: 65%;
}
}
@media only screen and (max-width: 56.25em) {
.book__form {
        width: 100%;
}
}






























































































































































































































































































